#root {
  @apply h-screen bg-gradient-to-b from-sky-200/30 from-0% via-sky-100/30 via-20% to-white to-50%;
}

.active::after {
  @apply h-[4px] absolute bg-gradient-to-r from-sky-200 to-sky-400 to-100% bottom-[-2px] left-0 animate-underline;
  content: '';
}

h1 {
  @apply font-semibold text-3xl my-2 mb-4 text-center;
}

h2 {
  @apply font-normal text-2xl my-2 mb-3 text-start;
}

h3 {
  @apply font-normal text-xl my-2 mb-3 text-start;
}

p {
  @apply text-base font-light text-start my-2;
}

.recharts-legend-item-text {
  @apply text-sm;
}

.recharts-legend-wrapper {
  @apply p-4 left-6;
}

.recharts-legend-item {
  @apply w-3/12 sm:w-auto flex gap-4 items-start justify-start;
}
